      <div>
        <v-layout row wrap>
          <v-flex xs12 sm12 class='text-xs-center'>
            <div class='mb-3'>
              <v-chip label :color="stateTabQl !== 'doanh_nghiep_van_tai' ? 'orange' : 'blue darken-1'" text-color='white' class='mr-2' @click="changeTab('doanh_nghiep_van_tai')">
                Doanh nghip vn ti
              </v-chip>
              <v-chip label :color="stateTabQl !== 'phuong_tien' ? 'orange' : 'blue darken-1'" text-color='white' class='mr-2' @click="changeTab('phuong_tien')">
                Phng tin
              </v-chip>
              <v-chip label :color="stateTabQl !== 'giay_phep_van_tai_quoc_te' ? 'orange' : 'blue darken-1'" text-color='white' class='mr-2' @click="changeTab('giay_phep_van_tai_quoc_te')">
                Giy php vn ti quc t
              </v-chip>
              <v-chip label :color="stateTabQl !== 'giay_phep_lien_van' ? 'orange' : 'blue darken-1'" text-color='white' class='mr-2' @click="changeTab('giay_phep_lien_van')">
                Giy php lin vn
              </v-chip>
              <!-- <v-chip label :color="stateTabQl !== 'phu_hieu_lien_van' ? 'orange' : 'blue darken-1'" text-color='white' class='mr-2' @click="changeTab('phu_hieu_lien_van')">
