        vm.genInland(result)
      }).catch(xhr => {
        console.log(xhr)
        toastr.error('Yu cu ca bn c thc hin tht bi.')
        vm.gpkdDialog = false
      })
    },
    generateCode (dossierTemplateNo) {
      var vm = this
      return new Promise((resolve, reject) => {
        vm.$store.dispatch('loadDossierTemplate', {
          dossierTemplateNo: dossierTemplateNo
        }).then(result => {
          var dossierParts = result
          var typeCode = ''
          if (dossierParts.length) {
            for (var i = 0; i < dossierParts.length; i++) {
              if (dossierParts[i].typeCode) {
                typeCode = dossierParts[i].typeCode
              }
            }
          }
          console.log('---------typeCode+++++++++', typeCode)
          if (typeCode) {
            vm.$store.dispatch('loadDeliverableTypes', {}).then(deliverableTypes => {
              if (deliverableTypes) {
                for (var i = 0; i < deliverableTypes.length; i++) {
                  if (deliverableTypes[i].deliverableType === typeCode) {
                    vm.$store.dispatch('generateCode', {
                      deliverableTypeId: deliverableTypes[i].deliverableTypeId
                    }).then(generatecode => {
                      console.log('---------generatecode+++++++++', generatecode)
                      resolve(generatecode)
                    }).catch(xhr => {
                      reject(xhr)
                    })
                    break
                  }
                }
              }
            }).catch(xhr => {
              reject(xhr)
            })
          }
        }).catch(xhr => {
          reject(xhr)
        })
      })
    },
    parseDate (date) {
      if (!date) return null
      console.log(date)
      const [year, month, day] = date.split('-')
      return `${day.padStart(2, '0')}/${month.padStart(2, '0')}/${year}`
    },
    genInland (items) {
      var vm = this
      vm.$store.dispatch('getInLand', items).then(response => {
        if (response.hasOwnProperty('formTemplate')) {
          vm.popUpPrintTraCuu = !vm.popUpPrintTraCuu
          if (response.formTemplate !== '' && response.formTemplate.length !== 0) {
            vm.currentPrintTemplate = {
              printTemplateId: response.printTemplateId,
              serviceCode: response.serviceCode,
              dossierPartNo: response.dossierPartNo,
              fileTemplateNo: response.fileTemplateNo,
              templateNo: response.templateNo,
              createUserId: response.createUserId,
              employeeId: response.employeeId,
              formTemplate: response.formTemplate,
              defaultCss: response.defaultCss,
              originalDocumentURL: response.originalDocumentURL,
              licenceType: response.licenceType
            }
            $('#printTraCuu').empty()
            var item
            console.log(response.formData)
            if (!response.formData) {
              item = items
            } else {
              console.log('hasForm!')
              item = JSON.parse(response.formData)
              console.log(item)
            }
            var originalDocumentArr = JSON.parse(response.originalDocumentURL)
            var originalDocumentObj = originalDocumentArr[0]
            for (var i = 0; i < originalDocumentArr.length; i++) {
              var originalDocumentTemp = originalDocumentArr[i]
              if (originalDocumentTemp.hasOwnProperty('licenceType') && originalDocumentTemp['licenceType'] === item.licenceType) {
                originalDocumentObj = originalDocumentTemp
                break
              }
            }
            $('#imgTraCuu').attr('src', originalDocumentObj.url)
            $('#imgTraCuu').css(originalDocumentObj.css)
            var imgTraCuu = $('#imgTraCuu')
            imgTraCuu.load(function () {
              var style = 'background-image: url(' + originalDocumentObj.url + ');background-size:' + imgTraCuu.width() + 'px ' + imgTraCuu.height() + 'px;'
              style += response.defaultCss
              $('#printTraCuu').attr('style', style)
              $('#printTraCuu').height(imgTraCuu.height())
              $('#printTraCuu').width(imgTraCuu.width())
            })
            var formTemplate = response.formTemplate
            var formData = JSON.parse(formTemplate)
            for (var key in formData) {
              if (!document.getElementById(key)) {
                var cssDiv = ''
                var newKeyArr = ''
                var newKey = ''
                if (formData[key].hasOwnProperty('css')) {
                  cssDiv = formData[key].css
                }
                if (key.indexOf('array_') !== -1) {
                  newKeyArr = key.split('_')
                  newKey = newKeyArr[2]
                  if (item.hasOwnProperty(newKey)) {
                    var itemDataArr = item[newKey].split('/')
                    if (itemDataArr.length > newKeyArr[1]) {
                      $('#printTraCuu').append('<div id=' + key + ' title=' + key + ' style="' + cssDiv + ';z-index: 99; position:absolute; left : ' + formData[key].offsetX + 'px; top : ' + formData[key].offsetY + 'px">' + itemDataArr[newKeyArr[1]] + '</div>')
                    }
                  }
                } else if (key.indexOf('_') !== -1) {
                  console.log('Key contain _: ' + key)
                  newKeyArr = key.split('_')
                  newKey = newKeyArr[1]
                  if (item.hasOwnProperty(newKey)) {
                    $('#printTraCuu').append('<div id=' + key + ' title=' + key + ' style="' + cssDiv + ';z-index: 99; position:absolute; left : ' + formData[key].offsetX + 'px; top : ' + formData[key].offsetY + 'px">' + item[newKey] + '</div>')
                  }
                } else {
                  if (key.toLowerCase().indexOf('text') !== -1 && formData[key].hasOwnProperty('value')) {
                    $('#printTraCuu').append('<div id=' + key + ' title=' + key + ' style="' + cssDiv + ';z-index: 99; position:absolute; left : ' + formData[key].offsetX + 'px; top : ' + formData[key].offsetY + 'px">' + formData[key].value + '</div>')
                  } else if (item.hasOwnProperty(key)) {
                    $('#printTraCuu').append('<div id=' + key + ' title=' + key + ' style="' + cssDiv + ';z-index: 99; position:absolute; left : ' + formData[key].offsetX + 'px; top : ' + formData[key].offsetY + 'px">' + item[key] + '</div>')
                  }
                }
                $('#' + key).draggable({
                  stop: function () {
                    var position = $(this).position()
                    var xPos = position.left
                    var yPos = position.top
                    formData[this.id].offsetX = xPos
                    formData[this.id].offsetY = yPos
                    var data = {
                      serviceCode: response.serviceCode,
                      dossierPartNo: response.dossierPartNo,
                      fileTemplateNo: response.fileTemplateNo,
                      templateNo: response.templateNo,
                      createUserId: vm.initData.userId,
                      employeeId: 0,
                      formTemplate: JSON.stringify(formData),
                      defaultCss: response.defaultCss,
                      originalDocumentURL: response.originalDocumentURL,
                      licenceType: response.licenceType
                    }
                    if (response.createUserId !== 0) {
                      data.printTemplateId = response.printTemplateId
                      var url = '/o/il/v2/inland'
                      axios.put(url + '/' + response.printTemplateId, $.param(data), {
                        headers: {
                          'Content-Type': 'application/x-www-form-urlencoded',
                          groupId: 55301
                        }
                      }).then(function (response) {
                        console.log(response)
                      }).catch(function (xhr) {
                        console.log(xhr)
                      })
                    } else {
                    }
                  }
                })
              }
            }
          }
        } else {
          vm.popUpPrintTraCuu = false
          vm.$store.dispatch('toDetailGiayPhep', items).then(result => {
            window.open(result)
          })
        }
      }).catch(xhr => {
      })
    },
    printGiayPhep (item) {
      var vm = this
      vm.popUpPrintTraCuu = !vm.popUpPrintTraCuu
      setTimeout(function () {
        vm.genInland(item)
      }, 500)
    },
    printDeliverable () {
      printJS({ printable: 'printTraCuu', type: 'html', scanStyles: false })
      // $('#printTraCuu').printThis()
    },
    savePrintTemplate () {
      var vm = this
      var formTemplate = {}
      $('#printTraCuu > div').each(function () {
        var id = $(this).attr('id')
        var xPos = $(this).position().left
        var yPos = $(this).position().top
        if (id.startsWith('text')) {
          formTemplate[id] = {
            offsetX: xPos,
            offsetY: yPos,
            value: $(this).text()
          }
        } else {
          formTemplate[id] = {
            offsetX: xPos,
            offsetY: yPos
          }
        }
      })
      var url = '/o/il/v2/inland'
      vm.currentPrintTemplate.formTemplate = JSON.stringify(formTemplate)
      vm.currentPrintTemplate.employeeId = 0
      if (vm.currentPrintTemplate.createUserId === 0) {
        axios.post(url, $.param(vm.currentPrintTemplate), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            groupId: 55301
          }
        }).then(function (response) {
          vm.popUpPrintTraCuu = !vm.popUpPrintTraCuu
        }).catch(function (xhr) {
        })
      } else {
        axios.put(url + '/' + vm.currentPrintTemplate.printTemplateId, $.param(vm.currentPrintTemplate), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            groupId: 55301
          }
        }).then(function (response) {
        }).catch(function (xhr) {
        })
      }
    }
  },
  filters: {
  }
}
</script>



